import axios from '@axios'

export default {
  namespaced: true,
  state: {
    tabla: {
      totalRows: 0,
      from: 0,
      to: 0,
    },
    talleresSegunDeporte: [],
    taller_global_id: null,
    tituloModal: '',
    tituloBoton: '',
    guardarEditar: true,
  },
  getters: {},
  mutations: {
    /* listarTalleresSegunDeporte(state, valor) {
      console.log('valor.data=', valor.data)
      state.talleresSegunDeporte = valor.data
      state.tabla.totalRows = valor.total || 0
      state.tabla.from = valor.from || 0
      state.tabla.to = valor.to || 0
    }, */
  },
  actions: {
    fetchProgramas(ctx, params) {
      console.log('params=> ', params)
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/taller-global/${params.id}/taller/`, { withCredentials: false })
          .then(response => { console.log('responseTalleres', response.data); ctx.commit('listarTalleresSegunDeporte', response.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    /* fetchSemestres() {
      return new Promise((resolve, reject) => {
        axios
          .get('/curso-taller-semestres', { withCredentials: false })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    }, */
    addCursoTaller(ctx, data) {
      console.log('CTN2= ', data)
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-deportivo/taller-global/${ctx.state.taller_global_id}/taller/`, data)
          .then(response => { console.log('exitoso', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    updateCursoTaller(ctx, data) {
      console.log('data = ', data)
      return new Promise((resolve, reject) => {
        axios
          .put(`/servicio-deportivo/taller/${data.id}`, data)
          .then(response => { console.log('exitoso', response); resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteCursoTaller(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-deportivo/taller-global/${ctx.state.taller_global_id}/taller/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    fetchTallerGlobal(ctx, { id }) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/taller-global/${id}`)
          .then(response => { console.log('fetchTallerGlobal: ', response); resolve(response.data.data) })
          .catch(error => reject(error))
      })
    },
  },

}
