<template>
  <b-container
    fluid
    class="bg-white pb-2 pt-1"
  >
    <MatricularEstudianteModal />
    <h3 class="text-primary pb-2 pt-1 my-1">
      <feather-icon icon="LayersIcon" />  Estudiantes del Programa Deportivo de alta Competencia :
      <b-badge variant="light-primary">
        FÚTBOL
      </b-badge>
    </h3>
    <!-- User Interface controls -->
    <b-row class="my-1">
      <!-- Per Page -->
      <b-col
        cols="12"
        md="6"
        class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
      >
        <b-button
          size="md"
          variant="outline-primary"
          class="mb-1"
          @click="abrirModal"
        >
          <feather-icon icon="PlusCircleIcon" /> Adicionar Estudiante
        </b-button>
      </b-col>

      <!-- Search -->
      <b-col
        cols="12"
        md="6"
      >
        <b-form-group
          label=""
          label-for="filter-input"
          label-cols-sm="3"
          label-align-sm="right"
          class="mb-0"
        >
          <b-input-group>
            <b-form-input
              id="filter-input"
              v-model="filter"
              type="search"
              placeholder="Buscar..."
            />
            <b-input-group-append>
              <b-button
                :disabled="!filter"
                variant="success"
                @click="filter = ''"
              >
                Limpiar
              </b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col
        sm="5"
        md="8"
        class=""
      >
        <label>Mostrar</label>
        <b-form-select
          id="per-page-select"
          v-model="perPage"
          :options="pageOptions"
          size="sm"
          class="per-page-selector d-inline-block mx-50"
          style="width:auto"
          @change="actualizarNumerosTabla"
        />
        <label>registros</label></b-col>

      <b-col
        sm="7"
        md="4"
        class="mb-1"
      >
        <b-pagination
          v-model="currentPage"
          :total-rows="tabla.totalRows"
          :per-page="perPage"
          align="fill"
          size="sm"
          class="my-0"
          @change="actualizarNumerosTabla"
        />
      </b-col>
    </b-row>

    <!-- Main table element -->
    <b-table
      :items="actividadesDeportivas"
      :fields="fields"
      :current-page="currentPage"
      :per-page="perPage"
      :filter="filter"
      :filter-included-fields="filterOn"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
      :sort-direction="sortDirection"
      stacked="md"
      head-variant="light"
      class="position-relative"
      show-empty

      hover
      responsive
      :empty-text="'No hay registros para mostrar'"
      :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
      @filtered="onFiltered"
    >
      <!-- <template #cell(name)="row">
        {{ row.value.first }} {{ row.value.last }}
      </template> -->
      <template #cell(estado)="row">
        <b-badge
          pill
          :variant="row.value.variant"
        >
          {{ row.value.status }}
        </b-badge>
      </template>
      <template #cell(acciones)="row">
        <b-dropdown
          variant="link"
          no-caret
          :right="$store.state.appConfig.isRTL"
        >
          <template #button-content>
            <feather-icon
              icon="MoreVerticalIcon"
              size="16"
              class="align-middle text-body"
            />
          </template>
          <b-dropdown-item>
            <feather-icon icon="EditIcon" />
            <span class="align-middle ml-50">Avance</span>
          </b-dropdown-item>
          <b-dropdown-item>
            <feather-icon icon="EditIcon" />
            <span class="align-middle ml-50">Competencias</span>
          </b-dropdown-item>
          <b-dropdown-item
            @click="eliminarCursoTaller(row.item)"
          >
            <feather-icon icon="TrashIcon" />
            <span class="align-middle ml-50">Eliminar</span>
          </b-dropdown-item>
        </b-dropdown>
      </template>

    </b-table>

    <span class="text-muted">Mostrando registros del {{ tabla.from }} al {{ tabla.to }} de un total de {{ tabla.totalRows }} registros</span>

  </b-container>
</template>

<script>
import {
  BContainer, BDropdown, BDropdownItem, BBadge, BFormSelect, BButton, BFormGroup, BFormInput, BRow, BCol, BInputGroup, BPagination, BTable, BInputGroupAppend,
} from 'bootstrap-vue'
import store from '@/store'

import { onUnmounted } from '@vue/composition-api'
import MatricularEstudianteModal from '@/views/servicio-deportivo/alta-competencia/MatricularEstudiante_modal_papelera.vue'
import { mapState } from 'vuex'
import altaCompetenciaStoreModule from './altaCompetenciaStoreModule'

export default {

  components: {
    MatricularEstudianteModal,
    BButton,
    BDropdown,
    BDropdownItem,
    BFormGroup,
    BFormInput,
    BContainer,
    BRow,
    BCol,
    BInputGroup,
    BFormSelect,
    BPagination,
    BTable,
    BInputGroupAppend,
    BBadge,
  },

  data() {
    return {
      id: this.$attrs.id,
      items: [],
      // totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: 'Ver todo' }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
      actividadesDeportivas: [
        {
          estudiante: 'Qhesya Rivadeneira Villogas', etnia: 'ASHANINKA', fecha: '15-04-2020', estado: { status: 'ACTIVO', variant: 'light-success' }, carrera: 'Carrea Profesional de Educación Primaria Bilingüe',
        },
        {
          estudiante: 'Edwin Fernandez Gonzales', etnia: 'ASHANINKA', fecha: '15-04-2020', estado: { status: 'ACTIVO', variant: 'light-success' }, carrera: 'Carrea Profesional de Educación Primaria Bilingüe',
        },
        {
          estudiante: 'Miguel Julian Villegas', etnia: 'ASHANINKA', fecha: '15-04-2020', estado: { status: 'INACTIVO', variant: 'light-danger' }, carrera: 'Carrea Profesional de Educación Primaria Bilingüe',
        },
        {
          estudiante: 'Javier Domingues Cruz', etnia: 'ASHANINKA', fecha: '15-04-2020', estado: { status: 'INACTIVO', variant: 'light-danger' }, carrera: 'Carrea Profesional de Educación Primaria Bilingüe',
        },
        {
          estudiante: 'Daniela Cruz Geronimo', etnia: 'ASHANINKA', fecha: '15-04-2020', estado: { status: 'ACTIVO', variant: 'light-success' }, carrera: 'Carrera Profesional de Ingeniería Agroforestal Acuícola',
        },
        {
          estudiante: 'Ever Donato Raymundo', etnia: 'ASHANINKA', fecha: '15-04-2020', estado: { status: 'ACTIVO', variant: 'light-success' }, carrera: 'Carrera Profesional de Ingeniería Agroforestal Acuícola',
        },
        {
          estudiante: 'Luz Yaneth Tello Saldaña', etnia: 'ASHANINKA', fecha: '15-04-2020', estado: { status: 'ACTIVO', variant: 'light-success' }, carrera: 'Carrera Profesional de Ingeniería Agroforestal Acuícola',
        },
        {
          estudiante: 'Zuly Pizarro Coz', etnia: 'ASHANINKA', fecha: '15-04-2020', estado: { status: 'ACTIVO', variant: 'light-success' }, carrera: 'Carrera Profesional de Ingeniería Agroforestal Acuícola',
        },
        {
          estudiante: 'Meliza Vigilio Gonzales', etnia: 'ASHANINKA', fecha: '15-04-2020', estado: { status: 'ACTIVO', variant: 'light-success' }, carrera: 'Carrera Profesional de Ingeniería Agroforestal Acuícola',
        },
      ],
    }
  },
  computed: {
    actualizarvar() {
      console.log('currentPage= ', this.currentPage)
      console.log('perPage= ', this.perPage)
      return console.log('actualizado')
    },

    ...mapState('talleres', ['tabla', 'talleresSegunDeporte', 'tituloModal', 'tituloBoton', 'guardarEditar', 'cursoTallerNuevo']),
  },
  mounted() {

    /* store.state.talleres.tabla.totalRows = store.state.talleres.talleresSegunDeporte.length
    store.state.talleres.taller_global_id = this.id */
  },
  methods: {
    actualizarNumerosTabla() {
      console.log('mensaje: Falta ver los numeros tabla')
    },
    cambioPaginacion(val) {
      console.log('pagina: ', val)
    },
    cambioCantMostrar(value) {
      console.log('cantMostrar: ', value)
    },
    resetInfoModal() {
      this.infoModal.title = ''
      this.infoModal.content = ''
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      store.state.talleres.tabla.totalRows = filteredItems.length
      this.currentPage = 1
    },
    traerSemestres() {
      store.dispatch('talleres/fetchSemestres').then(response => {
        console.log('semestres=', response.data)
        store.state.talleres.semestres_confechas = response.data
        // eslint-disable-next-line no-plusplus
        for (let i = 0; i < response.data.length; i++) {
          const objeto = { value: response.data[i].id, text: response.data[i].nombre }
          store.state.talleres.semestres.push(objeto)
        }
      })
    },
    abrirModal() {
      /* store.state.talleres.tituloModal = 'Nuevo Curso/Taller'
      store.state.talleres.tituloBoton = 'Registrar Taller'
      store.state.talleres.guardarEditar = true
      store.state.talleres.cursoTallerNuevo.nombre = ''

      this.traerSemestres()

      this.$bvModal.show('modal_nuevo_cursoTaller') */
      this.$bvModal.show('modal_matricula_estudiante_taller')
    },
    editarCursoTaller(item) {
      this.traerSemestres()
      store.state.talleres.cursoTallerNuevo.id = item.id
      store.state.talleres.cursoTallerNuevo.nombre = item.nombre
      store.state.talleres.cursoTallerNuevo.semestre = item.semestre_id
      store.state.talleres.cursoTallerNuevo.f_inicio = item.f_inicio
      store.state.talleres.cursoTallerNuevo.f_fin = item.f_fin
      store.state.talleres.cursoTallerNuevo.profesor = item.profesor_id

      store.state.talleres.tituloModal = 'Editar Taller'
      store.state.talleres.tituloBoton = 'Actualizar'
      store.state.talleres.guardarEditar = false

      this.$bvModal.show('modal_nuevo_cursoTaller')
    },
    eliminarCursoTaller(value) {
      console.log('id_eliminar', value)
      this.$swal({
        title: `Esta seguro de eliminar el taller ${value.nombre}`,
        text: 'Esta acción es irreversible!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, eliminar!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('talleres/deleteCursoTaller', value.id).then(response => {
            console.log(response)
            this.$swal({
              icon: 'success',
              title: 'Realizado!',
              text: 'Taller eliminado.',
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
            store.dispatch('talleres/fetchTalleres', { id: this.id })
          }).catch(error => {
            console.log(error)
          })
        }
      })
    },
  },
  setup() {
    const TALLERES_APP_STORE_MODULE_NAME = 'talleres'

    // Register module
    if (!store.hasModule(TALLERES_APP_STORE_MODULE_NAME)) store.registerModule(TALLERES_APP_STORE_MODULE_NAME, altaCompetenciaStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(TALLERES_APP_STORE_MODULE_NAME)) store.unregisterModule(TALLERES_APP_STORE_MODULE_NAME)
    })

    const resolveCitaStatusVariant = estado => {
      if (estado === 1) return 'success'
      if (estado === 0) return 'danger'
      return 'primary'
    }
    const fields = [

      { key: 'estudiante', label: 'Estudiante', sortable: true },
      { key: 'carrera', label: 'CarreraProfesional', sortable: true },
      { key: 'etnia', label: 'Etnia', sortable: true },
      { key: 'fecha', label: 'FechaRegistro', sortable: true },
      { key: 'estado', label: 'Estado', sortable: true },
      { key: 'acciones', label: 'acciones' },
    ]
    return {
      fields,
      resolveCitaStatusVariant,
    }
  },
}
</script>
